/* open-sans-regular - latin-ext_latin */
@font-face {
  font-family: "CV-SH-OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url(/static/media/open-sans-v15-latin-ext_latin-regular.81d0487b.woff2)
      format("woff2"),
    
      url(/static/media/open-sans-v15-latin-ext_latin-regular.2b6f63fc.woff)
      format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* open-sans-700 - latin-ext_latin */
@font-face {
  font-family: "CV-SH-OpenSans";
  font-style: normal;
  font-weight: 700;
  src: url(/static/media/open-sans-v15-latin-ext_latin-700.15df1fb3.woff2)
      format("woff2"),
    
      url(/static/media/open-sans-v15-latin-ext_latin-700.efe9ead0.woff)
      format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

html {
  font-family: "CV-SH-OpenSans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.55;
  color: #222;
}

@media screen and (min-width: 630px) {
  html {
    font-size: 18px;
  }
}

@media screen and (min-width: 960px) {
  html {
    font-size: 20px;
  }
}

body {
  margin: 0;
  padding: 0;
}

::selection {
  background: #004b5f;
  color: white;
  text-shadow: none;
}

@media print {
  html {
    -webkit-print-color-adjust: exact;
    color-adjust: exact;
    font-size: 10pt;
  }

  @page {
    size: A4;
    margin: 12mm 15mm 20mm 35mm;
  }
}

a {
  text-decoration: none;
  color: #456ead;
}
a:hover {
  color: inherit;
}

@media print {
  a[href^="http"]:not(.print-no-href)::after {
    content: " (" attr(href) ")";
  }
}

/* thx github */
pre,
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  background-color: #f6f8fa;
  border-radius: 3px;
  font-size: 85%;
}
code {
  margin: 0;
  padding: 0.2em 0.4em;
}
pre {
  padding: 1em;
  overflow: auto;
  line-height: 1.45;
}

em,
strong,
b {
  font-style: normal;
  font-weight: 700;
  color: #004b5f;
}

h1,
h2,
h3,
h4 {
  color: #002f58;
  font-weight: 700;
  margin-top: 1rem;
  margin-bottom: 0;
}

@media print {
  h1,
  h2,
  h3,
  h4 {
    page-break-before: avoid;
    page-break-after: avoid;
    page-break-inside: avoid;
  }
}

h1,
h3 {
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

h1,
h2 {
  border-top: 0.5rem solid #002f58;
  padding-top: 0.5em;
}

h1 {
  font-size: 1.2rem;
  margin-bottom: 4rem;
  color: #004b5f;
  margin-top: 0;
}

h2 {
  font-size: 1rem;
  margin-top: 2rem;
  margin-bottom: 1.5rem;
  padding-bottom: 0.8rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 9;
}

@media print {
  h2 {
    position: static;
  }
}

h2 small {
  color: #002f58;
  font-style: normal;
  font-weight: 400;
}

h3 {
  font-size: 0.8rem;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
  color: black;
}
h4 {
  font-size: 1rem;
}

ul,
ol {
  padding-left: 1em;
  margin: 0;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

ul {
  list-style: none;
}

li::before {
  content: "\B7";
  color: #004b5f;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

p {
  color: #222;
  margin-top: 0;
  margin-bottom: 0.2rem;
}


.small,
small {
  font-size: 0.8rem;
}

hr {
  border: none;
  height: 0.5rem;
  background-color: #002f58;

  margin-bottom: 2.3rem;
  margin-top: 2rem;
}

